
.App {
  text-align: center;
  background-image: url('https://masteringnuxt.com/images/bg-coming-soon-tablet.png');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: right top;
  background-color: #282c34;
}

.App-title {
  display: flex;
  align-items: center;
  padding: 0 0 10% 0;
}

.App-title h2 {
  font-size: 10vmin;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  font-size: 1.5rem;
  border: 0;
  background-color: transparent;
  border: 1px solid #61dafb;
  cursor: pointer;
  padding: 5px 10px;
}

.App-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.App-input {
  padding: 0.5rem 0;
  font-size: 1rem;
  border: 0;
  border-bottom: 1px solid #61dafb;
  background-color: transparent;
  color: #fff;
  margin-right: 20px;
}

.App-input:focus {
  outline: none;
}